import React, { createContext, useState } from "react";
import Apply from "./Apply";
import Description from "./Description";
import Option from "./Option";
import Browse from "./Browse";

export const StageContext = createContext();

// Landing is the primary component for the 3-screen flow that
//   students start out in. It asks them if they're applying or
//   just browsing, then shows them some dropdowns and redirects.

export default function Landing() {
  // landing has 3 stages: intro, apply, and browse
  const [stage, setStage] = useState("intro");

  // determine what to render on the left side
  const renderLeft = () => {
    switch (stage) {
      case "intro":
        return <Option pos={"left"} />;
      default:
        return <Description />;
    }
  };

  // determine what to render on the right side
  const renderRight = () => {
    switch (stage) {
      case "intro":
        return <Option pos={"right"} />;
      case "apply":
        return <Apply />;
      case "browse":
      default:
        return <Browse />;
    }
  };

  return (
    <StageContext.Provider value={{ stage, setStage }}>
        <div className="landing-container-container">
          <div className="landing-container" id="mainWrapper">
            <div className="left-container" id="leftContainer">
              {renderLeft()}
            </div>
            <div className="right-container" id="rightContainer">
              {renderRight()}
            </div>
          </div>
        </div>
    </StageContext.Provider>
  );
}
