import React, { useContext } from "react";
import { StageContext } from "./Landing";

export default function Description() {
  const { stage, setStage } = useContext(StageContext);

  const subtitle =
    stage === "apply" ? "Great! Lets do this!" : "Awesome! Lets get started.";

  const title =
    stage === "apply"
      ? "Which program are you attending?"
      : "Tell us about your interests!";

  const color = stage === "apply" ? "description browse-disc text-white" : "description apply-disc text-white";

  return (
    <>
      <div className={color}>
        <div className="description-text">
          <button className="back-button" onClick={() => setStage("intro")}>
            Back
          </button>
          <div>
            <p>{subtitle}</p>
            <h1>{title}</h1>
          </div>
        </div>
      </div>
    </>
  );
}
