import React from "react";
import { Dropdown } from "semantic-ui-react";
import { schools } from "../../data/data";
import { Link } from "react-router-dom";
import _ from "lodash";

export default function Apply() {
  const schoolData = _.map(schools, (s) => {
    return {
      key: s,
      value: s,
      text: s,
    };
  });

  return (
    <div className="school">
      <div className="school-text-container">
        <div className="school-text">
          <p>School Name</p>
          <Dropdown
            placeholder="Choose the school you're attending"
            fluid
            search
            selection
            className="selector"
            options={schoolData}
          />
        </div>
        <Link to="/meratas">
          <button className="button-center button-large green-button">Apply Now</button>
        </Link>
      </div>
    </div>
  );
}
