import React, { useContext } from "react";
import { StageContext } from "./Landing";

export default function Option({ pos }) {
  const { setStage } = useContext(StageContext);

  const title =
    pos === "left"
      ? "I know the program I'd like to apply to."
      : "I need help finding a good program.";

  const buttonLabel = pos === "left" ? "Apply Directly" : "Browse Programs";

  const target = pos === "left" ? "apply" : "browse";

  let classNames = pos === "left" ? "left-option shadow text-white" : "right-option shadow text-white";

  return (
    <div className={classNames}>
      <div>
        <h1 className="option-header"> {title} </h1>
        <button className="button-large green-button" onClick={() => setStage(target)}>{buttonLabel}</button>
      </div>
    </div>
  );
}
