import React from "react";
import { Dropdown } from "semantic-ui-react";
import { locations, jobs, styles, lengths } from "../../data/data";
import _ from "lodash";
import { Link } from "react-router-dom";

export default function Browse() {
  const locationData = _.map(locations, (s) => {
    return {
      key: s,
      value: s,
      text: s,
    };
  });

  const fields = Object.keys(jobs);

  const programsData = [];

  const fieldData = _.map(fields, (s) => {
    jobs[s].forEach((p) =>
      programsData.push({
        key: p,
        value: p,
        text: p,
      })
    );

    return {
      key: s,
      value: s,
      text: s,
    };
  });

  const stylesData = _.map(styles, (s) => {
    return {
      key: s,
      value: s,
      text: s,
    };
  });

  const lengthsData = _.map(lengths, (s) => {
    return {
      key: s,
      value: s,
      text: s,
    };
  });

  return (
    <div className="browse">
      <div className="drop-down-container">
        <div className="drop-down">
          <p>Where are you located?</p>
          <Dropdown
            placeholder="Choose your city"
            search
            selection
            className="selector"
            options={locationData}
          />
        </div>

        <div className="drop-down">
          <p>What fields are you interested in working in?</p>
          <Dropdown
            placeholder="Pick a field"
            search
            selection
            className="selector"
            options={fieldData}
          />
        </div>

        <div className="drop-down">
          <p>What programs would you like to look at?</p>
          <Dropdown
            placeholder="Pick a job in that field"
            search
            multiple
            selection
            className="selector"
            options={programsData}
          />
        </div>

        <div className="drop-down">
          <p>How do you prefer to learn?</p>
          <Dropdown
            placeholder="Online vs. in-person"
            search
            selection
            className="selector"
            options={stylesData}
          />
        </div>

        <div className="drop-down">
          <p>How long should the program be?</p>
          <Dropdown
            placeholder="Program length"
            search
            multiple
            selection
            className="selector"
            options={lengthsData}
          />
        </div>

        <div>
          <Link to="/explore">
            <button className="button-large green-button button-center">Search</button>
          </Link>
        </div>
      </div>
    </div>
  );
}
